

.header {
    width: 100%;
    max-width: 100%;
    height: 5rem;
    display: flex;
    justify-content: space-between;
    background-color: #f5951f;
    margin-right: 100px;
    background-attachment: fixed;
    
  }

  .logo {
    font-size: 2rem;
    color: white;
    font-weight: bold;
    padding-left: 50px;

  }
  
  .header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }
  
  .header li {
    margin-left: 2rem;
  }
  
  .header a {
    text-decoration: none;
    font-size: 20px;
    color: #000000;
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: white;
  }
  
  .badge {
    background-color: #cc2062;
    color: white;
    border-radius: 12px;
    padding: 0 1rem;
    margin-left: 0.5rem;
  }